<template>
    <div class="back-manager">
        <div class="">
            <el-row>
                <el-select v-model="value" placeholder="请选择">
                    <el-option
                            v-for="item in projects"
                            :key="item.ID"
                            :label="item.APP_NAME"
                            :value="item.APP_ID">
                    </el-option>
                </el-select>
            </el-row>
            <el-row>
                <input @change="selectFileChange($event)" class="xm-top-20" accept="image/png,image/jpeg" type="file">
            </el-row>
            <el-button class="xm-top-20" @click="checkIsUpload()">上传图片</el-button>
        </div>
    </div>
</template>

<script>
    export default {
        name: "back-manager",
        data() {
            return {
                value: "",
                projects: [],
                selectFile:null
            }
        },
        methods:{
            checkIsUpload(){
                if(this.selectFile == null){
                    this.$message.error("没有选择图片");
                    return;
                }
                let imgName = this.selectFile.name;
                const query = this.Bmob.Query("T_IMG");
                query.equalTo("name","===", imgName);
                query.find().then(res => {
                    console.log(res);
                    if(res.length === 0){
                        this.uploadImg();
                    }
                    else {
                        this.$message.error(imgName+"已经上传过了");
                    }
                });
            },
            uploadImg(){
                if(this.selectFile == null){
                    this.$message.error("没有选择图片");
                    return;
                }
                if(this.value === ""){
                    this.$message.error("没有选择项目");
                    return;
                }

                let file = this.Bmob.File(this.selectFile.name, this.selectFile);
                file.save().then(res => {
                    console.log(res);
                    if(res.length > 0){
                        let img_url = res[0].url;
                        const query = this.Bmob.Query("T_IMG");
                        query.set("app_id", this.value);
                        query.set("img_url", img_url);
                        query.set("name", res[0].filename);
                        query.save().then(res => {
                            console.log(res);
                        }).catch(err => {
                            console.log(err);
                        });
                        this.$message.success("成功");
                    }
                })
            },
            selectFileChange(el){
                this.selectFile = el.target.files[0];
                console.log(this.selectFile);
            },
            getProjects() {
                const query = this.Bmob.Query("T_APP");
                query.find().then(res => {
                    console.log(res);
                    this.projects = res;
                    if(this.projects.length > 0){
                        this.value = this.projects[0].APP_ID;
                    }
                });
            }
        },
        created(){
            this.getProjects();
        }
    }
</script>

<style lang="scss">
    .back-manager {
        width: 80%;
        margin: 60px auto 0;
    }
</style>